  letter-spacing: 0.07em;
  text-transform: uppercase;
  color: #f2f5f7;
  margin-bottom: 30px;
  margin-top: 0;
}
@media (max-width: 1023px) {
  .page-header h1 {
    font-size: 64px;
    line-height: 75px;
    margin-bottom: 0;
  }
}
@media (max-width: 769px) {
  .page-header h1 {
    margin-bottom: 8px;
