  .page-header p {
    margin-bottom: 57px;
  }
}
@media (max-width: 769px) {
  .page-header p {
    width: 270px;
    font-size: 14px;
    margin-bottom: 43px;
  }
}

.page-header__wrapper-info {
  position: absolute;
  left: 25%;
  top: 35%;
}
@media (max-width: 1023px) {
  .page-header__wrapper-info {
    top: 28%;
    left: 6.5%;
  }
}
@media (max-width: 769px) {
  .page-header__wrapper-info {
    top: 27%;
    left: 8%;
  }
}

.page-header__wrapper-navigation {
  background: linear-gradient(180deg, #041827 84.9%, rgba(4, 24, 39, 0) 100%);
  padding: 20px 0 35px 0;
}
@media (max-width: 1919px) {
  .page-header__wrapper-navigation {
    padding: 20px 100px 35px;
  }
}
@media (max-width: 1023px) {
  .page-header__wrapper-navigation {
    padding: 10px 70px 25px 50px;
  }
}
@media (max-width: 769px) {
  .page-header__wrapper-navigation {
    padding: 8px 35px 20px 26px;
  }
}

.page-header__wrapper-info a {
  display: flex;
  justify-content: center;
  width: 410px;
  background: #00b2ff;
  border: none;
  border-radius: 3px;
  padding: 18px 10px;
}
@media (max-width: 1023px) {
  .page-header__wrapper-info a {
    width: 410px;
    margin-left: 45px;
  }
}
@media (max-width: 769px) {
  .page-header__wrapper-info a {
    padding: 13px 25px;
    width: 270px;
    font-size: 14px;
    line-height: 16px;
    margin-left: 0;
  }
}
.page-header__wrapper-info a:hover {
  background: #025375;
}
.page-header__wrapper-info a:hover span {
  background: #025375;
}
.page-header__wrapper-info a:focus {
  border: none;
  outline: 1px solid #56c3b2;
}
.page-header__wrapper-info a:active {
  opacity: 0.6;
}

.page-header__wrapper-info a > span {
  background: #00b2ff;
  font-weight: 700;
  font-size: 18px;
  line-height: 21px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #f2f5f7;
}
@media (max-width: 769px) {
  .page-header__wrapper-info a > span {
    font-size: 14px;
    line-height: 16px;
  }
}

.page-header__wrapper-info a > span:nth-child(1) {
  display: block;
}
@media (max-width: 769px) {
  .page-header__wrapper-info a > span:nth-child(1) {
    display: none;
  }
}

.page-header__wrapper-info a > span:nth-child(2) {
  display: none;
  width: 270px;
}
@media (max-width: 769px) {
  .page-header__wrapper-info a > span:nth-child(2) {
    display: flex;
    justify-content: center;
  }
