  .navigation {
    padding-right: 0;
  }
}

.navigation a {
  display: flex;
  align-items: center;
  color: #f2f5f7;
  font-family: "PT Mono", "Arial", sans-serif;
  font-size: 16px;
  line-height: 18px;
  padding: 10px 0;
}
.navigation a span {
  padding-left: 15px;
}
@media (max-width: 1023px) {
  .navigation a span {
    display: none;
  }
}
.navigation a svg {
