  .navigation__logo-desktop {
    display: none;
  }
}

.navigation__logo-mobile {
  display: none;
}
@media (max-width: 769px) {
  .navigation__logo-mobile {
    display: flex;
  }
}

a:hover .navigation__logo-mobile,
