.modal__content form p {
  margin-bottom: 40px;
  font-size: 14px;
  line-height: 16px;
}

.modal__content button[type=button] {
  position: absolute;
  top: 15px;
  right: 15px;
  width: 40px;
  height: 40px;
  min-width: 40px;
  min-height: 40px;
  padding: 0;
  background-color: #00b3ff;
  border: none;
}
.modal__content button[type=button] svg {
  margin: auto;
}

.wrapper-check--modal button {
  margin-right: 0;
  margin-bottom: 20px;
  padding: 14px 10px;
}

button:focus {
  border: 2px solid #ffffff;
}

